.tracking-report-container .table-responsive {
  position: relative;
  z-index: 0;
}
.tracking-report-container .select-wrapper {
  width: 300px;
}
.tracking-report-container .control-label {
  margin-right: 10px;
}
.tracking-report-container .form-type-select .select-wrapper:after {
  top: 29px;
}

.tracking-report-table .no-results-message td {
  text-align: center;
  padding: 20px;
  font-style: italic;
  background-color: #f5f5f5;
}

.tracking-report-filters .select-wrapper {
  width: 100px;
}

.tracking-search-form .form-item {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  gap: 10px;
}
.tracking-search-form .form-item label {
  flex: 0 0 150px;
  text-align: right;
  margin: 0;
  padding-right: 10px;
}
.tracking-search-form .form-item input[type=text],
.tracking-search-form .form-item input[type=date],
.tracking-search-form .form-item select {
  flex: 0 0 300px;
  width: 300px;
  min-height: 45px;
}
.tracking-search-form .form-type-select .select-wrapper {
  width: 300px;
}
.tracking-search-form .form-type-select .select-wrapper select,
.tracking-search-form .form-type-select .select-wrapper select option {
  background-color: #FFF;
}
.tracking-search-form #edit-back {
  margin-bottom: 15px;
}
.tracking-search-form .panel-heading {
  margin-bottom: 0;
}
.tracking-search-form h3 {
  margin-top: 0;
}
.tracking-search-form.panel-group .panel-default {
  margin-bottom: 20px;
}
.tracking-search-form.panel-group .panel-title {
  padding: 0;
}
.tracking-search-form.panel-group .panel-title a {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  padding: 10px 15px;
}
.tracking-search-form.panel-group .panel-title a:not(.collapsed) .fa-caret-down {
  transform: rotate(180deg);
}

#block-fws-raf-mainnavigation {
  display: none;
}

header#navbar #navbar-collapse {
  border-top: 0;
  box-shadow: none;
}
@media (min-width: 992px) {
  header#navbar .navbar-header {
    min-width: 250px;
  }
}
@media (max-width: 991px) {
  header#navbar > .container {
    width: 100%;
  }
}

@media (max-width: 991px) {
  .mb-menuwrapper {
    height: calc(100% - 115px);
    top: 115px;
  }
}

.fws-branding__logo img {
  width: 100%;
}

.navbar-main {
  width: auto;
  margin-bottom: 0;
}
.navbar-main .navbar-nav {
  float: right;
  margin-top: 20px;
}
.navbar-main .navbar-nav > li > a {
  font-size: 1.062em;
  font-weight: 700;
  text-transform: uppercase;
}
.navbar-main .dropdown-menu {
  background-color: #fff;
  list-style: none;
  padding: 0;
}
.navbar-main .dropdown-menu > li > a {
  background-color: #fff;
  color: #333;
  padding: 10px 15px;
}
.navbar-main .dropdown-menu > li > a:hover, .navbar-main .dropdown-menu > li > a:focus {
  background-color: #337ab7;
  color: #fff;
}

.navbar-default .navbar-nav > .active > a {
  color: #fff;
}
.navbar-default .navbar-nav > .active > a:hover, .navbar-default .navbar-nav > .active > a:focus {
  color: #fff;
}

.views-element-container .view-content .views-field a {
  white-space: nowrap;
}
.views-element-container .panel-title a {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.views-element-container .panel-title a:not(.collapsed) .fa-caret-down {
  transform: rotate(180deg);
}
.views-element-container .panel-body {
  padding: 20px;
}

.views-exposed-form .form-type-select .select-wrapper select,
.views-exposed-form .form-type-select .select-wrapper select option {
  min-width: 85px;
  min-height: 36px;
}
.views-exposed-form .form-type-select .select-wrapper:after {
  top: 27px;
  right: 0;
  z-index: 10;
}
.views-exposed-form .form-item {
  margin-right: 20px;
  margin-bottom: 10px;
}
.views-exposed-form .form-item label {
  margin-right: 10px;
}
.views-exposed-form .form-item .form-text {
  max-width: 150px;
}
.views-exposed-form .form-actions {
  margin-bottom: 10px;
}

.select2.select2-container {
  height: 45px !important;
  line-height: 45px !important;
}

.select2-selection.select2-selection--single {
  height: 45px !important;
  border: 1px solid #ccc !important;
  border-radius: 4px !important;
  display: flex !important;
  align-items: center !important;
}

.select2-selection__clear {
  height: 100% !important;
  display: flex !important;
  align-items: center !important;
  padding: 0 8px !important;
  border-right: 1px solid #ccc !important;
  margin: 0 !important;
}

.select2-selection__rendered {
  height: 100% !important;
  display: flex !important;
  align-items: center !important;
  padding-left: 8px !important;
}

.select2-selection__arrow {
  height: 100% !important;
  display: flex !important;
  align-items: center !important;
  top: 0 !important;
  right: 5px !important;
}